import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import style from './components/style/Style.scss'
import Tool from './components/./Tool'
// import OperationModal from './components/./OperationModal'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import {
  API_PRODUCTIONFREQUENCYS_WORKSHOP_LIST,
  API_GET_PRODUCTION_CODEANDNAME_CRAFT,
  API_GET_LOCATION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_FEETYPE_LIST,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
} from 'api'
import { CsxFormSelect, CsxFormSearch } from 'Form'
import { getUrlDataObj, countTableScroll } from 'util'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Table,
  InputNumber,
  message
} from 'antd'
const FormItem = Form.Item
const mapType = {
  FY001: 'FY001',
  FY002: 'FY002',
  FY003: 'FY003'
}
@connect((state) => ({ ...state.industryLineDet }), mapDispatchToProps)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      editOrCreate: '',
      permissionBlooean: '' // 是否允许编辑  'allowEdit' -- false 、 'forbiddenEdit' -- true
    }
  }
  componentDidMount() {
    const { location = {}, match = {}, update, getSingleDet } = this.props
    const { search = '' } = location
    const { params = '' } = match
    const { id = '' } = params
    const { editOrCreate = '', permissionFlag = [] } = getUrlDataObj(
      search || ''
    )
    let tableDataSource = [
      {
        id: Date.now(),
        workTime: '8',
        craftType: '',
        unit: { value: '1', children: '小时' },
        number: '1'
      }
    ]
    if (id) {
      //编辑
      const params = {
        urlParams: [`${id}`]
      }
      getSingleDet(params, (data) => {
        console.log('get detail data is', data)
      })
    }
    update({ tableDataSource })
    this.setState({
      editOrCreate,
      permissionBlooean: permissionFlag === 'allowEdit' ? true : false
    })
  }
  componentWillUnmount() {
    const { update } = this.props
    update({
      modalData: '',
      tableDataSource: ''
    })
  }
  handleSelectChange = (typeSource, data) => {
    const { update, modalData = {} } = this.props
    if (typeSource == 'location') {
      // 更改地点编码 -- 筛选地点编码下的商品
      update({
        modalData: { ...modalData, location: data }
      })
    }
    if (typeSource == 'craftType') {
      // 更改地点编码 -- 筛选地点编码下的商品
      update({
        modalData: { ...modalData, location: data }
      })
    }
  }
  handleSelectChangeCraftType = (index, data) => {
    const { update, tableDataSource = [] } = this.props
    const { value = '' } = data
    let objArr = [...tableDataSource]
    let arrForUnique = []
    objArr[index].craftType = data
    objArr[index].craftTypeName = value
    objArr &&
      objArr.length > 0 &&
      objArr.map((item) => {
        const { craftType = {} } = item
        const { value = '' } = craftType
        return arrForUnique.push(value)
      })
    update({
      tableDataSource: [...objArr],
      tableDataSelected: [...new Set(arrForUnique)]
    })
  }
  getAsyncDateExtension = (source, data) => {
    let result = []
    const { tableDataSource = [] } = this.props
    if (source && source == 'dataInTable') {
      result =
        data &&
        data.length > 0 &&
        data.filter((da) => {
          let flag = true
          tableDataSource &&
            tableDataSource.length > 0 &&
            tableDataSource.map((item) => {
              const { craftType = '' } = item
              const { value = '' } = craftType
              if (value == da.code) {
                flag = false
              }
            })
          return flag
        })
    } else {
      return data
    }
    return result
  }
  handleSubmit = (e) => {
    const {
      form,
      modalData,
      handleAddOrEdit,
      history,
      location = {}
    } = this.props
    const { search = '' } = location
    const { editOrCreate = '' } = getUrlDataObj(search || '')
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('form values is', values)
        // return 111
        let hasCraftAbilityList = false
        let reqObj = { ...values }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'location') {
              reqObj.location = reqObj[i].children
            }
            if (i == 'line') {
              reqObj.line = reqObj[i].children
            }
            if (i == 'workshop') {
              reqObj.workshop = reqObj[i].value
            }
            if (i == 'product') {
              reqObj.product = reqObj[i].children
            }
          }
          if (i == 'craftAbilityList') {
            hasCraftAbilityList = true
            let modalVal = reqObj[i]
            console.log('modalVal is', modalVal)
            let obj = {}
            modalVal &&
              Array.isArray(modalVal) &&
              modalVal.map((item) => {
                const { craftType = {} } = item
                const { value = '' } = craftType
                if (value === mapType[value]) obj[value] = true
                return null
              })
            if (obj.FY001 && obj.FY002 && obj.FY003) {
              let resaultList = []
              modalVal &&
                modalVal.length > 0 &&
                modalVal.map((item) => {
                  const {
                    craftType = {},
                    unit = {},
                    number = '',
                    id = ''
                  } = item
                  let param = {}
                  if (editOrCreate == 'edit') {
                    param = {
                      craftType: (craftType && craftType.value) || '',
                      unit: (unit && unit.value) || '',
                      number: number || '',
                      id: id || ''
                    }
                  } else {
                    param = {
                      ...item,
                      craftType: (craftType && craftType.value) || '',
                      unit: (unit && unit.value) || '',
                      number: number || ''
                    }
                  }
                  return resaultList.push(param)
                })
              reqObj.craftAbilityList = resaultList
            } else {
              return message.error('人工、机器、辅料3个类别项，缺一不可')
            }
          }
        }
        console.log(' reqObj is', reqObj, reqObj.craftAbilityList)
        if (!hasCraftAbilityList) {
          return message.error('人工、机器、辅料3个类别项，缺一不可')
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          // result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, () => {
          message.success(msg)
          history.push('/mms/settingCenter/productManage/industryLine')
        })
      }
    })
  }
  range = (start, end) => {
    const result = []
    for (let i = start; i < end; i++) {
      result.push(i)
    }
    return result
  }

  disabledHours = () => {
    const hours = this.range(0, 60)
    hours.splice(20, 4)
    return hours
  }

  disabledMinutes = (h) => {
    if (h === 20) {
      return this.range(0, 31)
    } else if (h === 23) {
      return this.range(30, 60)
    }
    return []
  }
  render() {
    const { tableLoading, history, selectedRowKeys } = this.props
    const {
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData = {},
      tableDataSource = []
    } = this.props
    const { editOrCreate, permissionBlooean } = this.state
    let msg =
      editOrCreate == 'edit'
        ? '工艺路线编辑'
        : editOrCreate == 'create'
        ? '工艺路线新增'
        : ''
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    const unitOptions = [
      { key: '1', value: '小时' }
      // { key: '2', value: '元' }
    ]
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    const columns = [
      {
        title: '类别',
        width: 150,
        dataIndex: 'craftType',
        key: 'craftType',
        render: (text, record, index) => {
          console.log('text is', text, index)
          return (
            <div className="box_in_table">
              <FormItem
              // hasFeedback
              >
                {getFieldDecorator(`craftAbilityList['${index}'][craftType]`, {
                  initialValue: text ? text : '',
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    // specialItem='dataInTable'
                    disabled={!permissionBlooean}
                    dateApi={API_PRODUCTIONFREQUENCYS_FEETYPE_LIST}
                    selectItem={{ key: 'code', valueKey: ['name'] }}
                    handleSelectChange={this.handleSelectChangeCraftType.bind(
                      this,
                      index
                    )}
                    getAsyncDateExtension={this.getAsyncDateExtension}
                  />
                )}
              </FormItem>
            </div>
          )
        }
      },
      {
        title: '单位',
        width: 150,
        dataIndex: 'unit',
        key: 'unit',
        render: (text, record, index) => (
          <div className="box_in_table">
            <FormItem>
              {getFieldDecorator(`craftAbilityList[${index}][unit]`, {
                initialValue: text ? text : '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  allowClear={true}
                  disabled={!permissionBlooean}
                  data={unitOptions}
                  selectItem={{ key: 'key', valueKey: 'value' }}
                  handleSelectChange={this.handleSelectChange}
                />
              )}
            </FormItem>
          </div>
        )
      },
      {
        title: '数量',
        width: 150,
        dataIndex: 'number',
        key: 'number',
        render: (text, record, index) => {
          const { id = '' } = record
          return (
            <div className="box_in_table">
              <FormItem hasFeedback>
                {getFieldDecorator(`craftAbilityList[${index}][number]`, {
                  initialValue: text ? text : '',
                  rules: [
                    {
                      required: true,
                      // message: '请输入正数且不超过两位小数!',
                      message: ' ',
                      pattern: /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$|(^0$)/
                    }
                  ]
                })(
                  <Input
                    disabled={!permissionBlooean}
                    placeholder="请填写0或正数"
                  />
                )}
              </FormItem>
              {id ? (
                <FormItem
                // hasFeedback
                >
                  {getFieldDecorator(`craftAbilityList[${index}][id]`, {
                    initialValue: id || ''
                  })(<Input type={'hidden'} />)}
                </FormItem>
              ) : (
                ''
              )}
            </div>
          )
        }
      }
    ]

    return (
      <section>
        <Bread route={['设置中心', '商品管理', '工艺路线设置', msg]} />
        <div className={style.table_group}>
          <Form
            onSubmit={this.handleSubmit}
            autoComplete="off"
            className={style.form}
          >
            <Row type="flex" justify="start">
              {
                <Col span={24}>
                  <Row type="flex" justify="end">
                    <Col>
                      {permissionBlooean && (
                        <Button
                          type="primary"
                          htmlType="submit"
                          style={{
                            marginRight: '0px',
                            marginBottom: '12px',
                            width: '80px'
                          }}
                          loading={modalTuttonLoading}
                        >
                          保存
                        </Button>
                      )}
                    </Col>
                  </Row>
                </Col>
              }

              <Col span={7}>
                <FormItem {...formItemLayout} label="地点" hasFeedback>
                  {getFieldDecorator('location', {
                    initialValue: modalData.location,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxFormSelect
                      allowClear={true}
                      disabled={!permissionBlooean}
                      dateApi={API_GET_LOCATION_CODEANDNAME}
                      selectItem={{
                        data: modalData.location,
                        key: 'code',
                        valueKey: ['code', 'name']
                      }}
                      handleSelectChange={this.handleSelectChange.bind(
                        this,
                        'location'
                      )}
                    />
                  )}
                </FormItem>
              </Col>
              <Col span={7}>
                <FormItem {...formItemLayout} label="所属车间" hasFeedback>
                  {getFieldDecorator('workshop', {
                    initialValue: modalData.workshop,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxFormSelect
                      allowClear={true}
                      disabled={!permissionBlooean}
                      dateApi={API_PRODUCTIONFREQUENCYS_WORKSHOP_LIST}
                      selectItem={{
                        data: modalData.workshop,
                        key: 'codeAndName',
                        valueKey: ['name']
                      }}
                      handleSelectChange={this.handleSelectChange.bind(
                        this,
                        'workshop'
                      )}
                    />
                  )}
                </FormItem>
              </Col>
              <Col span={7}>
                <FormItem {...formItemLayout} label="产线" hasFeedback>
                  {getFieldDecorator('line', {
                    initialValue: modalData.line,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxFormSelect
                      allowClear={true}
                      disabled={!permissionBlooean}
                      dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
                      selectItem={{
                        key: 'codeAndName',
                        valueKey: ['code', 'name']
                      }}
                      handleSelectChange={this.handleSelectChange.bind(
                        this,
                        'line'
                      )}
                    />
                  )}
                </FormItem>
              </Col>
              <Col span={7}>
                <FormItem {...formItemLayout} label="商品编码/名称">
                  {getFieldDecorator('product', {
                    initialValue: modalData.product,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxFormSearch
                      dateApi={API_GET_PRODUCTION_CODEANDNAME_CRAFT}
                      showSearch={false}
                      initData={modalData.product}
                      searchParamsKey="productCode"
                      disabled={
                        modalData && modalData.location
                          ? permissionBlooean
                            ? false
                            : true
                          : true
                      }
                      extraParamsKey={{
                        locationCode:
                          (modalData &&
                            modalData.location &&
                            modalData.location.value) ||
                          ''
                      }}
                      selectItem={{
                        key: 'codeAndName',
                        valueKey: ['code', 'name']
                      }}
                      onChangeSelect={() => {}}
                      onSelect={() => {}}
                    />
                  )}
                </FormItem>
              </Col>
            </Row>
            <hr className={style.table_tite_line} />
            <Row type="flex" justify="start">
              <Col span={24}>
                <div className={style.table_titel}>成本核算相关</div>
              </Col>
              <Col span={7}>
                <FormItem
                  {...formItemLayout}
                  label="数量因子"
                  // hasFeedback
                >
                  {getFieldDecorator('reckonFactor', {
                    initialValue: modalData.reckonFactor,
                    rules: [
                      {
                        required: true,
                        // message: ' ',
                        message: '请填写正整数!',
                        pattern: /^[1-9]\d*$/
                      }
                    ]
                  })(<InputNumber disabled={!permissionBlooean} />)}
                </FormItem>
              </Col>
              <Col span={24}>
                {permissionBlooean && (
                  <Tool history={history} form={this.props.form} />
                )}
              </Col>
            </Row>
            <Row>
              <div style={{ minHeight: '150px' }}>
                <div className="table-group table_detail table_70">
                  <Table
                    rowKey={(record) => record.id}
                    loading={tableLoading}
                    columns={columns}
                    dataSource={tableDataSource}
                    scroll={{ x: countTableScroll(columns) }}
                    onChange={this.onChange}
                    pagination={false}
                    rowSelection={rowSelection}
                  />
                </div>
              </div>
              {editOrCreate === 'create' ? null : (
                <div className={style.table_group}>
                  <Col span={6}>
                    <Row>
                      <Col span={10} className={style.bottom_tip}>
                        创建人:
                      </Col>
                      <Col span={14}>{modalData && modalData.createBy}</Col>
                    </Row>
                  </Col>
                  <Col span={6}>
                    <Row>
                      <Col span={10} className={style.bottom_tip}>
                        创建时间:
                      </Col>
                      <Col span={14}>{modalData && modalData.createTime}</Col>
                    </Row>
                  </Col>
                  <Col span={6}>
                    <Row>
                      <Col span={10} className={style.bottom_tip}>
                        最后更新人员:
                      </Col>
                      <Col span={14}>{modalData && modalData.updateBy}</Col>
                    </Row>
                  </Col>
                  <Col span={6}>
                    <Row>
                      <Col span={10} className={style.bottom_tip}>
                        最后更新时间:
                      </Col>
                      <Col span={14}>{modalData && modalData.updateTime}</Col>
                    </Row>
                  </Col>
                </div>
              )}
            </Row>
          </Form>
        </div>
      </section>
    )
  }
}
export default Form.create()(App)



