import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_INDUSTRYLINE_DETS_PAGE,
  API_INDUSTRYLINE_DETS_DETAIL,
  API_INDUSTRYLINE_DETS_CREATE,
  API_INDUSTRYLINE_DETS_UPDATE
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.INDUSTRYLINE_DET_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.INDUSTRYLINE_DET_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_INDUSTRYLINE_DETS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.INDUSTRYLINE_DET_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_DET_FAIL
      })
    })
}

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.INDUSTRYLINE_DET_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_INDUSTRYLINE_DETS_UPDATE
      : API_INDUSTRYLINE_DETS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.INDUSTRYLINE_DET_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_DET_FAIL
      })
    })
}
// 获取 - 单个属性数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  getData({ api: API_INDUSTRYLINE_DETS_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        lineCode = '',
        lineName = '',
        locationCode = '',
        locationName = '',
        workshopCode = '',
        workshopName = '',
        productName = '',
        productCode = '',
        craftAbilityList = []
      } = result
      if (craftAbilityList && craftAbilityList.length > 0) {
        craftAbilityList.map((item, index, arr) => {
          const {
            craftType = '',
            unit = '',
            craftTypeName = '',
            number = ''
          } = item
          let unitValue = unit == '1' ? '小时' : unit == '2' ? '元' : ''
          item.craftType = {
            value: `${craftType}`,
            children: `${craftTypeName}`
          }
          item.unit = { value: `${unit}`, children: `${unitValue}` }
          item.number = String(number)
        })
      }
      let dataFormater = {
        ...result,
        location: {
          value: `${locationCode}`,
          children: `${locationCode} ${locationName}`
        },
        line: {
          value: `${lineCode} ${lineName}`,
          children: `${lineCode} ${lineName}`
        },
        workshop: {
          value: `${workshopCode} ${workshopName}`,
          children: `${workshopName}`
        },
        product: {
          value: `${productCode} ${productName}`,
          children: `${productCode} ${productName}`
        }
      }
      console.log('craftAbilityList is', craftAbilityList)
      dispatch({
        type: actionType.INDUSTRYLINE_DET_UPDATE,
        data: {
          modalData: dataFormater,
          tableDataSource: craftAbilityList
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_DET_FAIL
      })
    })
}



